.services {
  padding-top: 61px;

  @include font($font-family_primary, $font-size_m, 400, $line-height_additional);
  color: $text-color_primary;

  background-color: $background-color_primary;

  .swiper-pagination {
    display: none;
  }
}

.services__inner {
  @include container_desktop;
}

.services__slider {
  background-color: $background-color_secondary;
}

.services__tabs-list {
  @include list-reset;

  display: flex;
}

.services__tabs-item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25%;
  min-height: 93px;

  @include font($font-family_primary, $font-size_m, 500, $line-height_m);
  color: $text-color_primary;

  @include transition;
  border-radius: 0;
  background-color: $background-color_primary;
  opacity: 0.8;
  cursor: pointer;

  svg {
    margin-right: 10px;

    fill: $background-color_accent;
  }

  &:hover {
    outline: none;
    opacity: 1;
  }

  &_active {
    opacity: 1;
    background-color: $background-color_secondary;
    outline: none;
  }
}

.services__slider-list {
  @include list-reset;
}

.services__slide {
  flex-shrink: 0;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  height: auto;
  min-height: 100%;
  padding-top: 53px;
  padding-right: 11.54%;
  padding-bottom: 60px;
  padding-left: 70px;

  background-color: $background-color_secondary;
}

.services__slide-text {
  flex: 0 1 42%;

  h3 {
    margin: 0;
    margin-bottom: 28px;

    font-size: $font-size_xl;
    font-weight: 500;
    line-height: $line-height_default;
  }

  ul {
    @include list-reset;

    margin-bottom: 35px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  li {
    margin-bottom: 15px;
    padding-left: 21px;

    font-size: $font-size_m;
    line-height: $line-height_additional;

    background-image: url("../img/icon-checkmark.svg");
    background-position: 0 6px;
    background-size: 13px 10px;
    background-repeat: no-repeat;

    &:last-child {
      margin-bottom: 0;
    }
  }

  p {
    margin: 0;
    font-size: $font-size_m;
    line-height: $line-height_default;
  }

  p > a {
    color: inherit;
  }
}

.services__slide-image {
  align-self: center;
  flex-basis: 45.6%;
  padding-top: 7px;

  img {
    display: block;
    width: 100%;
  }
}

@media (max-width: $max-width_tablet) {
  .services {
    padding-top: 0;

    .swiper-pagination {
      bottom: 15px;

      display: flex;
      justify-content: center;
      width: 100%;
    }

    .swiper-pagination-bullet {
      width: 8px;
      height: 8px;
      margin: 0 4px;

      background-color: $blue_darker;
      opacity: 1;
    }

    .swiper-pagination-bullet-active {
      background-color: $gray_light;
    }
  }

  .services__inner {
    max-width: 100%;
    padding: 0;
  }

  .services__tabs {
    display: none;
  }

  .services__slide {
    padding-right: 45px;
    padding-left: 45px;
  }

  .services__slide-text {
    flex-basis: 52%;

    h3 {
      margin-bottom: 24px;

      font-size: $font-size_m;
    }

    ul {
      margin-bottom: 30px;
    }

    li {
      margin-bottom: 16px;

      font-size: $font-size_s;
    }

    p {
      font-size: $font-size_s;
    }
  }

  .services__slide-image {
    flex-basis: 42.63%;
  }
}

@media (max-width: $max-width_mobile) {
  .services {
    .swiper-pagination {
      bottom: 10px;

      .swiper-pagination-bullet {
        margin: 0 3px;
      }
    }

    .swiper-pagination-bullet {
      width: 6px;
      height: 6px;
    }
  }

  .services__slide {
    position: relative;

    min-height: 362px;
    padding-right: 15px;
    padding-left: 15px;
    padding-bottom: 0;
  }

  .services__slide-text {
    flex-basis: 100%;
    position: relative;
    z-index: 2;

    h3 {
      margin-bottom: 22px;

      font-size: 16px;
      line-height: 140%;
    }

    ul {
      max-width: 80%;
      margin-bottom: 27px;
    }

    li {
      margin-bottom: 12px;
      padding-left: 15px;

      font-size: $font-size_xs;

      background-position: 0 4px;
      background-size: 10px 8px;
      background-repeat: no-repeat;
    }

    p {
      max-width: 90%;

      font-size: $font-size_xs;
    }

    .button {
      width: 100%;
      max-width: 290px;
    }
  }

  .services__slide-image {
    position: absolute;
    z-index: 1;

    top: 98px;
    right: 0;

    padding-top: 0;
  }
}
